.common-main-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  .left-container-wrap {
    flex: 0 0 240px;
    width: 240px;
    height: 100%;
    padding: 40px;
    box-sizing: border-box;
  }
  .right-container-wrap {
    flex: 1;
    height: 100%;
    padding: 40px;
    box-sizing: border-box;
    border-left: 2px solid #ececec;
    overflow-y: auto;
    .col-item {
      width: 100%;
      display: block;
    }
    .left-con, .right-con {
      width: 50%;
      display: inline-block;
    }
  }
}
