@charset "UTF-8";
body,
h1,
h2,
h3,
h4,
h5,
h6,
hr,
p,
blockquote,
dl,
dt,
dd,
ul,
ol,
li,
pre,
form,
fieldset,
legend,
button,
input,
textarea,
th,
td {
  margin: 0;
  padding: 0;
}
body,
button,
input,
select,
textarea {
  font: 14px "microsoft yahei";
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}
address,
cite,
dfn,
em,
var {
  font-style: normal;
}
code,
kbd,
pre,
samp {
  font-family: couriernew, courier, monospace;
}
small {
  font-size: 14px;
}
ul,
ol {
  list-style: none;
}
a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
legend {
  color: #000;
}
fieldset,
img {
  border: 0;
}
button,
input,
select,
textarea {
  font-size: 100%;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
* {
  margin: 0;
  padding: 0;
  font-family: "microsoft yahei";
  list-style: none;
  box-sizing: border-box;
}
body,
html,
#root {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
img {
  border: 0;
  outline: none;
}
a {
  text-decoration: none;
  border: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
  outline: none;
}
.App {
  text-align: left;
  width: 100%;
  height: 100%;
}
.ant-layout {
  height: 100%;
}
.ant-layout-content {
  background: #f5f5f5;
  overflow-y: auto;
  position: relative;
}
.ant-modal-body {
  padding: 20px 24px !important;
}
.dialog-wraper .ant-form-item {
  margin-bottom: 12px !important;
}
.main-container {
  padding-top: 0;
  padding-right: 20px;
  padding-bottom: 20px;
  padding-left: 20px;
  box-sizing: border-box;
  background: #f5f5f5;
  border-radius: 5px;
  position: relative;
}
.page-loading {
  position: absolute;
  top: 10px;
  right: 20px;
}
.operation-wraper,
.content-wraper {
  padding: 10px;
  border-radius: 5px;
  background-color: #FFFFFF;
}
.search-wraper {
  overflow: hidden;
  padding-top: 10px;
  padding-right: 10px;
  padding-bottom: 6px;
  padding-left: 10px;
  border-radius: 5px;
  background-color: #FFFFFF;
}
.search-wraper .ant-form-item {
  display: inline-block !important;
  margin-bottom: 0 !important;
  margin-right: 8px;
}
.search-wraper .ant-form-item .ant-form-item-label {
  padding: 0 0 4px !important;
  line-height: normal;
}
.ant-input[disabled],
.ant-select-disabled .ant-select-selection {
  color: rgba(0, 0, 0, 0.6) !important;
}
.ant-layout-sider {
  width: 220px !important;
  flex: 0 0 220px !important;
  max-width: 220px !important;
  min-width: 220px !important;
}
.ant-layout-sider-collapsed {
  width: 80px !important;
  flex: 0 0 80px !important;
  max-width: 80px !important;
  min-width: 80px !important;
}
.s-slip {
  border: 1px solid #f5f5f5;
}
.content-wraper {
  margin-top: 10px;
}
.search-input {
  min-width: 0 !important;
  width: 170px !important;
}
.search-input-short {
  min-width: 0 !important;
  width: 140px !important;
}
.search-date-picker {
  min-width: 0 !important;
  width: 180px !important;
}
.form-item-add {
  min-width: 0 !important;
  width: 320px !important;
}
.sp-handel-w {
  padding-left: 18px;
  padding-bottom: 10px;
  border-bottom: 1px solid #ececec;
}
.btn-wrap .ant-btn {
  margin-left: 10px;
}
.form-item-label {
  display: inline-block;
  width: 180px !important;
  text-align: right;
  color: rgba(0, 0, 0, 0.85);
  padding-right: 10px;
}
.from-item-required:before {
  content: "*";
  padding-right: 4px;
  color: #f5222d;
}
.nm {
  margin: 0;
}
.no-mt {
  margin-top: 0 !important;
}
.no-mb {
  margin-bottom: 0 !important;
}
.mt20 {
  margin-top: 20px;
}
.ml10 {
  margin-left: 10px !important;
}
.ml20 {
  margin-left: 20px !important;
}
.ant-btn-link {
  padding: 0 !important;
  padding-right: 10px !important;
}
.ant-notification-notice-description {
  max-height: 200px !important;
  overflow-y: auto !important;
  word-break: break-all;
}
.ant-tabs-bar {
  margin: 0 0 6px 0;
}
.footer-wrap {
  margin-top: 10px;
}

.header-wrap {
  width: 100%;
  height: 50px;
  background-color: #1890ff;
  position: relative;
  /*用户信息*/
}
.header-wrap .menu-control-btn-wrap {
  height: 50px;
  line-height: 50px;
  font-size: 20px;
  color: #FFFFFF;
  cursor: pointer;
  padding-left: 20px;
  padding-right: 20px;
}
.header-wrap .city-change-wraper {
  margin-top: 9px;
  margin-bottom: 9px;
  margin-right: 0;
  margin-left: 10px !important;
  float: left;
}
.header-wrap .per-mesg {
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 999;
  margin-top: -2px;
}
.header-wrap .per-mesg .username {
  position: relative;
  line-height: 48px;
}
.header-wrap .per-mesg .username span {
  color: #FFFFFF;
}
.header-wrap .per-mesg .username span:nth-child(1) {
  font-size: 20px;
  position: relative;
  top: 2px;
  right: 4px;
}
.header-wrap .per-mesg .username span:nth-child(2) {
  font-size: 16px;
  vertical-align: middle;
  width: 82px;
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  white-space: nowrap;
}
.header-wrap .per-mesg .username span:nth-child(3) {
  font-size: 14px;
  position: relative;
  top: 2px;
}
.header-wrap .per-mesg .handel-wrap {
  width: 100%;
  height: auto;
  background-color: #1890ff;
  border-radius: 5px;
  position: relative;
  top: 8px;
  padding-left: 8px;
  padding-right: 8px;
}
.header-wrap .per-mesg .handel-wrap .slow-icon {
  position: absolute;
  top: -14px;
  left: 6px;
  color: #1890ff;
}
.header-wrap .per-mesg .handel-wrap p {
  line-height: 38px;
  border-bottom: 1px solid #eeeeee;
  font-size: 14px;
  color: #FFFFFF;
  text-align: center;
  margin-bottom: 0;
}
.header-wrap .per-mesg .handel-wrap p:nth-child(3) {
  border-bottom: 0;
}

.logo-wraper {
  height: 50px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  background-color: #002140;
  padding-left: 20px;
  padding-right: 20px;
}
.logo-wraper .logo-pointer {
  font-size: 28px;
  color: #FFFFFF;
  font-weight: bold;
  padding-left: 4px;
  padding-right: 4px;
  position: relative;
  bottom: 8px;
}
.logo-wraper .sys-name-txt {
  font-size: 16px;
  color: #FFFFFF;
}
.se-txt-wrap {
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 4px;
  padding-bottom: 4px;
  color: #4b646f;
  background-color: #1a2226;
}
.iconfont-mh-icon {
  margin-right: 5px;
}
.ant-menu-inline-collapsed > .ant-menu-item .iconfont-mh-icon + span,
.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-item .iconfont-mh-icon + span,
.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-submenu > .ant-menu-submenu-title .iconfont-mh-icon + span,
.ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title .iconfont-mh-icon + span {
  display: inline-block;
  max-width: 0;
  opacity: 0;
}
.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-item .anticon + a,
.ant-menu-inline-collapsed > .ant-menu-item-group > .ant-menu-item-group-list > .ant-menu-submenu > .ant-menu-submenu-title .anticon + a,
.ant-menu-inline-collapsed > .ant-menu-item .anticon + a,
.ant-menu-inline-collapsed > .ant-menu-submenu > .ant-menu-submenu-title .anticon + a {
  display: inline-block;
  max-width: 0;
  opacity: 0;
}

.login-wraper {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("/imgs/login_bg.jpg");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  background-attachment: fixed;
  position: relative;
}
.login-wraper .login-form-wrap {
  background-color: #FFFFFF;
  border-radius: 8px;
  position: relative;
  padding: 40px;
  margin-top: -150px;
}
.login-wraper .login-form-wrap .sys-tip-txt {
  width: 100%;
  text-align: center;
  font-size: 20px;
  margin-bottom: 30px;
  color: #666666;
}
.login-wraper .login-form-wrap .form-title-line {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.login-wraper .login-form-wrap .form-title-line .form-title-line-left,
.login-wraper .login-form-wrap .form-title-line .form-title-line-right {
  width: 80px;
  border-top: 1px dotted #cccccc;
}
.login-wraper .login-form-wrap .form-title-line .form-title-line-center {
  padding-left: 10px;
  padding-right: 10px;
}
.login-wraper .login-form-wrap .form-title-line .form-title-line-center:hover {
  color: #1890ff;
}
.login-wraper .login-form-wrap .login-form {
  min-width: 340px;
  max-width: 440px;
}
.login-wraper .login-form-wrap .login-form .login-form-button {
  width: 100%;
  margin-top: 20px;
}
.login-wraper .login-form-wrap .login-form .login-err-txt-tip {
  text-align: center;
  color: #f5222d;
  position: absolute;
  bottom: 20px;
  left: 0;
  width: 100%;
}
.login-wraper .login-form-wrap .login-form .ant-form-item {
  margin-bottom: 20px;
}
.login-wraper .login-form-wrap:hover {
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
  border-top: 2px solid #1890ff;
  box-shadow: 0 2px 15px #eeeeee;
  background-color: #FFFFFF;
  -webkit-transition: all 0.2s linear;
  transition: all 0.2s linear;
}
.login-wraper .copy-p {
  position: absolute;
  left: 0;
  bottom: 60px;
  text-align: center;
  width: 100%;
  font-size: 14px;
  color: #FFFFFF;
  letter-spacing: 0;
}

.common-main-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
}
.common-main-container .left-container-wrap {
  flex: 0 0 240px;
  width: 240px;
  height: 100%;
  padding: 40px;
  box-sizing: border-box;
}
.common-main-container .right-container-wrap {
  flex: 1 1;
  height: 100%;
  padding: 40px;
  box-sizing: border-box;
  border-left: 2px solid #ececec;
  overflow-y: auto;
}
.common-main-container .right-container-wrap .col-item {
  width: 100%;
  display: block;
}
.common-main-container .right-container-wrap .left-con,
.common-main-container .right-container-wrap .right-con {
  width: 50%;
  display: inline-block;
}

